/* @include cubic-transition($delay: 0, $duration: 200ms, $property: (all)); */
.flexbox {
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
}

.no-flexbox {
  display: inherit;
}

.topnav {
  position: relative;
  background: #fff;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  transition: all 0.2s ease-in-out;
}
.topnav.sticky {
  box-shadow: 0 2px 15px rgba(0, 0, 0, 0.09);
}
.topnav.sticky .main-menu {
  top: 52px;
}
.topnav .flex_row {
  align-items: center;
  height: 80px;
  justify-content: space-between;
}
.topnav .left_menu {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 20px;
}
.topnav .logo {
  margin-bottom: 0;
}
.topnav .logo a {
  display: block;
  font-size: 0;
  padding: 10px;
}
.topnav .logo img {
  width: 178px;
}
.topnav #vibeji-ham {
  width: 25px;
  height: 20px;
  position: absolute;
  right: 15px;
  top: 22px;
  transform: rotate(0);
  transition: 0.5s ease-in-out;
  cursor: pointer;
  display: none;
  margin-right: 0 !important;
}
.topnav #vibeji-ham span {
  display: block;
  position: absolute;
  height: 2px;
  width: 100%;
  background: #00091c;
  border-radius: 9px;
  opacity: 1;
  right: 0;
  transform: rotate(0);
  transition: 0.25s ease-in-out;
}
.topnav #vibeji-ham span:nth-child(1) {
  top: 0;
  width: 55%;
}
.topnav #vibeji-ham span:nth-child(2) {
  top: 7px;
  width: 100%;
}
.topnav #vibeji-ham span:nth-child(3) {
  top: 14px;
  width: 75%;
}
.topnav #vibeji-ham.open span:nth-child(1) {
  top: 9px;
  width: 100%;
  transform: rotate(135deg);
}
.topnav #vibeji-ham.open span:nth-child(2) {
  opacity: 0;
  right: -30px;
  width: 100%;
}
.topnav #vibeji-ham.open span:nth-child(3) {
  top: 9px;
  width: 100%;
  transform: rotate(-135deg);
}
.topnav .main-menu .nav {
  align-items: center;
}
.topnav .main-menu .nav > li {
  margin-right: 40px;
  display: flex;
  align-items: center;
  position: relative;
}
.topnav .main-menu .nav > li > a {
  font-weight: 600;
  height: 50px;
  display: flex;
  align-items: center;
  position: relative;
}
.topnav .main-menu .nav > li .sub_menu {
  font-size: 0;
  margin-left: 8px;
  margin-top: -2px;
}
.topnav .main-menu .nav > li .sub_menu::before {
  content: "";
  border-bottom: 1px solid #00091c;
  border-right: 1px solid #00091c;
  width: 7px;
  height: 7px;
  transform: rotate(45deg);
  display: block;
}
.topnav .main-menu .nav > li.active > a {
  color: var(--colormain);
}
.topnav .main-menu .nav > li.default {
  position: relative;
}
.topnav .main-menu .nav > li.default .level2 {
  min-width: 200px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}
.topnav .main-menu .nav > li.default .level2 a {
  display: block;
  font-size: 15px;
  padding: 10px;
  border-bottom: 1px solid #dfdfdf;
}
.topnav .main-menu .nav > li.default .level2 a:last-child {
  border-bottom: 0;
}
@media (max-width: 991px) {
  .topnav .main-menu .nav > li.default .level2 {
    box-shadow: none;
    padding-left: 15px;
    padding-bottom: 5px;
  }
  .topnav .main-menu .nav > li.default .level2 a {
    padding: 10px 0;
  }
}
.topnav .main-menu .nav > li:last-child {
  margin-right: 0;
}
.topnav .main-menu .search-btn {
  margin-right: 20px;
}
.topnav .main-menu .search-btn .icon-svg {
  color: rgba(255, 255, 255, 0.85);
}
.topnav .main-menu .btn-default {
  line-height: 40px;
  font-size: 14px;
  font-weight: 600;
  padding: 0 16px;
}
@media (min-width: 992px) {
  .topnav .main-menu .level2 {
    opacity: 0;
    filter: alpha(opacity=0);
    visibility: hidden;
    overflow: hidden;
    position: absolute;
    top: 100%;
    left: 0;
    margin: 0;
    z-index: 10;
    background: #fff;
    text-align: left;
    padding: 0 10px;
    transition-duration: 200ms;
    transition-property: opacity, top, visible;
    transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
  }
  .topnav .main-menu .level2 .bg-white {
    padding: 48px 0;
    background: #fff;
    position: relative;
    z-index: 10;
  }
  .topnav .main-menu .level2 [class*=grid__] {
    column-gap: 40px;
    row-gap: 40px;
  }
  .topnav .main-menu .level2 .itemt {
    display: flex;
    flex-wrap: wrap;
  }
  .topnav .main-menu .level2 .itemt .ava {
    background: #fff;
    border-radius: 10px;
    position: relative;
    width: 56px;
    height: 56px;
  }
  .topnav .main-menu .level2 .itemt .ava img {
    max-height: 95%;
    max-width: 95%;
    width: auto;
    height: auto;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
  }
  .topnav .main-menu .level2 .itemt .content {
    width: 300px;
    width: calc(100% - 56px);
    padding-left: 24px;
  }
  .topnav .main-menu .level2 .itemt .content h3 {
    font-size: 16px;
    font-weight: bold;
  }
  .topnav .main-menu .level2 .itemt .content p {
    color: #555770;
    font-size: 13px;
  }
  .topnav .main-menu .nav > li:hover .level2 {
    opacity: 1;
    filter: alpha(opacity=100);
    visibility: visible;
    top: 0;
    margin-top: 50px;
  }
}
@media (max-width: 1199px) {
  .topnav .main-menu .nav > li {
    margin-right: 25px;
  }
}
@media (max-width: 991px) {
  .topnav .main-menu {
    position: fixed;
    top: 60px;
    bottom: 0;
    right: -100%;
    width: 100% !important;
    background-color: #fff !important;
    overflow-y: scroll;
    color: #4d4d4d;
    -webkit-transition: all 0.25s ease-in-out;
    transition: all 0.25s ease-in-out;
    z-index: 1001;
  }
  .topnav .main-menu .level2 {
    display: none;
  }
  .topnav .main-menu .level2 [class*=grid__] {
    grid-template-columns: repeat(1, 1fr);
    padding: 5px 1px 15px 15px;
  }
  .topnav .main-menu .level2 .itemt {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
  .topnav .main-menu .level2 .itemt .ava {
    background: #F5F7FA;
    border-radius: 10px;
    position: relative;
    width: 40px;
    height: 40px;
  }
  .topnav .main-menu .level2 .itemt .ava img {
    max-height: 95%;
    max-width: 95%;
    width: auto;
    height: auto;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
  }
  .topnav .main-menu .level2 .itemt .content {
    width: calc(100% - 40px);
    padding-left: 15px;
  }
  .topnav .main-menu .level2 .itemt .content h3 {
    font-size: 14px;
    margin-bottom: 0;
  }
  .topnav .main-menu .level2 .itemt .content p {
    display: none;
  }
  .topnav .main-menu .nav {
    display: block;
    padding: 0 15px;
  }
  .topnav .main-menu .nav > li {
    padding: 0;
    margin-right: 0;
    display: block;
    position: relative;
    border-bottom: 1px solid #e5e5e5;
  }
  .topnav .main-menu .nav > li > a {
    display: block;
    width: 100%;
    position: relative;
    border-radius: 0;
    height: 100%;
    line-height: 50px;
    color: #222;
  }
  .topnav .main-menu .nav > li > a::before {
    display: none;
  }
  .topnav .main-menu .nav > li .sub_menu {
    display: block;
    font-size: 22px;
    width: 40px;
    text-align: center;
    line-height: 50px;
    position: absolute;
    right: 0;
    top: 0px;
  }
  .topnav .main-menu .nav > li .sub_menu::before {
    display: none;
  }
  .topnav .main-menu .nav > li.active::before {
    display: none;
  }
  .topnav .main-menu .nav > li.active > a {
    font-weight: bold;
    color: var(--colormain);
  }
  .topnav .main-menu .search-btn {
    margin-right: 0;
    margin: 20px 0;
  }
  .topnav .main-menu .search-btn .auto_search_button {
    display: none;
  }
  .topnav .main-menu .search-btn .icon-svg {
    fill: #ccc;
  }
  .topnav .main-menu .search-btn .search-wrap {
    display: block;
    position: static;
    width: 100%;
  }
  .topnav .main-menu .search-btn .search-wrap input {
    border: 1px solid #e5e5e5;
    height: 40px;
  }
  .topnav .main-menu .search-btn .search-wrap button {
    top: 10px;
  }
  .topnav .main-menu .my-login .btn-default {
    width: 100%;
  }
  .topnav .main-menu.open {
    right: 0;
  }
}
.topnav .right_menu a {
  margin-left: 20px;
}
@media (max-width: 1199px) {
  .topnav .right_menu a {
    margin-left: 10px;
    padding: 5px 20px;
  }
  .topnav .right_menu .tra-cuu {
    display: none;
  }
}
@media (max-width: 991px) {
  .topnav .flex_row {
    height: 60px;
  }
  .topnav .right_menu {
    padding-right: 50px;
  }
  .topnav #vibeji-ham {
    display: block;
  }
}
@media (max-width: 767px) {
  .topnav .logo a {
    padding: 0;
  }
  .topnav .logo img {
    width: 140px;
  }
}

