.search-btn {
  position: relative;
}
.search-btn button {
  border: 0;
  background: none;
}
.search-btn .icon-svg {
  position: relative;
  display: block;
  width: 16px;
  height: 16px;
  top: 7px;
  color: #222;
  -webkit-transition-duration: 400ms;
  transition-duration: 400ms;
  -webkit-transition-property: all;
  transition-property: all;
  -webkit-transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
  transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
}
.search-btn .close {
  width: 13px;
  height: 13px;
  opacity: 0 !important;
}
.search-btn .auto_search_button.is-clicked .icon-svg {
  top: -13px;
  opacity: 0;
}
.search-btn .auto_search_button.is-clicked .close {
  opacity: 1 !important;
  top: -7px;
}

.search-wrap {
  position: absolute;
  top: -4px;
  right: 30px;
  width: 312px;
  height: 36px;
  z-index: 2;
  display: none;
  background: rgb(255, 255, 255);
  border-radius: 49px;
}
.search-wrap .container {
  background: #fff;
}
.search-wrap input {
  background: rgba(255, 255, 255, 0.1);
  font-size: 14px;
  height: 36px;
  width: 100%;
  outline: 0;
  border: 0;
  padding: 0 30px 0 20px;
  border-radius: 49px;
}
.search-wrap button {
  position: absolute;
  right: 12px;
  top: 9px;
  outline: 0;
}
.search-wrap button svg {
  fill: rgba(255, 255, 255, 0.85);
  top: 2px !important;
}

body.active .overlay {
  opacity: 1;
  z-index: 9;
}

.overlay {
  background: rgba(0, 0, 0, 0.4);
  width: 100%;
  position: fixed;
  height: 100%;
  top: 0;
  z-index: -1;
  opacity: 0;
  -moz-transition: 0.2s ease-out;
  -o-transition: 0.2s ease-out;
  -webkit-transition: 0.2s ease-out;
  -ms-transition: 0.2s ease-out;
  transition: 0.2s ease-out;
}